var staticRenderFns = [function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"row"},[_c('div',{staticClass:"col-md-12 hero-c"},[_c('div',{staticClass:"hero-i"},[_c('img',{staticClass:"img-fluid",staticStyle:{"height":"100%","width":"100%","opacity":"0.7"},attrs:{"src":require("../assets/hero.jpg")}})]),_c('div',{staticClass:"hero-t text-center"},[_c('h2',[_vm._v("发现明天")]),_c('h2',[_vm._v("Descubre el mañana")])])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"row justify-content-center mt-4",attrs:{"id":"contenidotot"}},[_c('div',{staticClass:"col-md-9"},[_c('div',{staticClass:"row"},[_c('div',{staticClass:"col-md-7"},[_c('h3',[_vm._v("Atenas 1896")]),_c('p',[_vm._v(" Durante el siglo XVIII, varios festivales deportivos a pequeña escala a lo largo de Europa se denominaron Juegos Olímpicos Antiguos. El barón Pierre de Coubertin tuvo la idea de revivir los antiguos Juegos Olímpicos, pero en forma de un evento multideportivo e internacional —los antiguos Juegos fueron en cierto modo internacionales, pues varias polis y colonias griegas tuvieron representación, pero solo se les permitió participar a los hombres libres de origen griego—,inspirandose en los juegos organizados por el hombre de negocios Evangelos Zappas en Grecia.El 18 de junio de 1894, Coubertin organizó un congreso en la Universidad de la Sorbona, en París, para presentar sus planes a los representantes de sociedades deportivas de once países. Después de la aprobación de la idea por parte del congreso, comenzó la elección de una fecha y un lugar. Coubertin sugirió que los Juegos se desarrollasen en 1900, coincidiendo con la Exposición Universal de París, pero como una espera de seis años podía hacer disminuir el interés del público, los miembros del congreso optaron por llevar a cabo los Juegos inaugurales en 1896. El proceso de elección de la sede es todavía un misterio debido a las versiones contradictorias que existen. El 23 de junio de 1894 Demetrius Vikelas propuso oficialmente Atenas y como Grecia fue el lugar donde nacieron los Juegos Olímpicos, la propuesta fue aprobada unánimemente. Vikelas además fue elegido presidente del recién creado Comité Olímpico Internacional. ")])]),_c('div',{staticClass:"col-md-5"},[_c('img',{staticClass:"img-fluid",attrs:{"src":require("../assets/atenas1896.jpeg")}})]),_c('div',{staticClass:"col-md-5 mt-3"},[_c('img',{staticClass:"img-fluid",attrs:{"src":require("../assets/paris1900.jpeg")}})]),_c('div',{staticClass:"col-md-7 mt-3"},[_c('h3',[_vm._v("París 1900")]),_c('p',[_vm._v(" Los Juegos Olímpicos de París 1900, oficialmente Juegos de la II Olimpiada, son la segunda edición de las olimpiadas modernas. Tuvieron lugar en París, Francia, del 14 de mayo al 28 de octubre de 1900, en el marco de la Exposición Universal. La decisión de celebrar los Juegos de 1900 en París se tomó en la reunión del I Congreso Olímpico celebrado en 1894.Tras los Juegos de 1896 Pierre de Coubertin, presidente del Comité Olímpico Internacional (COI), quería organizar los Juegos de la II Olimpiada, pero Alfred Picard, comisario general de la Exposición Universal quería organizar «competiciones internacionales de ejercicio físico y deportes» durante el evento. El por entonces órgano rector del deporte en Francia, la Union des sociétés françaises de sports athlétiques (USFSA), en noviembre de 1898 decidió finalmente celebrar las competiciones de la Exposición en lugar de los Juegos de Coubertin que, en la primavera de 1899 se vio obligado a aceptar la propuesta de la USFSA: «Las competiciones de la Exposición se celebran en lugar de los Juegos Olímpicos de 1900 y cuentan como equivalentes a la II Olimpiada.» No hubo ceremonia de apertura ni de clausura, las pruebas se desarrollaron a lo largo de cinco meses y estos eventos no se denominan «Juegos Olímpicos» en los documentos oficiales ni en los carteles promocionales, por lo que muchos atletas ignorarían, algunos hasta su muerte, que habían competido en los Juegos Olímpicos.Las pruebas deportivas atrajeron a 58 731 participantes, pero según el COI, solo 997 atletas de 24 países, entre ellos 22 mujeres, compitieron en disciplinas que considera olímpicas. ")])])])]),_c('div',{staticClass:"col-md-2"},[_c('table',{staticClass:"table table-striped"},[_c('thead',[_c('tr',[_c('th',{attrs:{"scope":"col"}},[_vm._v("Grandes atletas de la historia")])])]),_c('tbody',[_c('tr',[_c('th',{attrs:{"scope":"row"}},[_c('a',{attrs:{"href":""}},[_vm._v("Johnny Weissmuller")])])]),_c('tr',[_c('th',{attrs:{"scope":"row"}},[_c('a',{attrs:{"href":""}},[_vm._v("Emil Zatopek")])])]),_c('tr',[_c('th',{attrs:{"scope":"row"}},[_c('a',{attrs:{"href":""}},[_vm._v("Abebe Bikila")])])]),_c('tr',[_c('th',{attrs:{"scope":"row"}},[_c('a',{attrs:{"href":""}},[_vm._v("Bob Beamon")])])]),_c('tr',[_c('th',{attrs:{"scope":"row"}},[_c('a',{attrs:{"href":""}},[_vm._v("Carl Lewis")])])]),_c('tr',[_c('th',{attrs:{"scope":"row"}},[_c('a',{attrs:{"href":""}},[_vm._v("Jesse Owens")])])])])])])])}]

